import React,{ Component } from 'react';
import CardList from './CardList32';
import { demo } from './demo32';
import SearchBox from './SearchBox32';

class App extends Component {
    constructor() {    
        super();    
        this.state = {      
            demo32: demo,      
            searchField: ''    
        };  
    }  
    
    onSearchChange = event => {    
        this.setState({ searchField: event.target.value });  
    };  
    
    render() {    
        const filteredDemo = this.state.demo32.filter(demo => {      
            return demo.name        
            .toLowerCase()        
            .includes(this.state.searchField.toLowerCase());    
        });

    return (    
      <div className='tc'>      
        <h1>208410232-陳世裕-Web-Project</h1>
        <SearchBox searchChange={this.onSearchChange}/>      
        <CardList demo={filteredDemo} />;    
      </div>  
    );
};
}

export default App;